import { h } from "preact";
import { useState, useEffect, useRef } from "preact/hooks";
import styled from "styled-components";
import { DoneGreen } from "../utillity/Icons";
import "animate.css";

const Card = styled.div`
  width: 370px;
  background: transparent;
  border-radius: 9px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
`;

// const Done = styled.img`
//   width: 150px;
// `;

const Thankyou = styled.p`
  color: #77b28f;
  font-size: 1.6rem;
  font-weight: 600;
  white-space: nowrap;
  text-align: center;
  margin-bottom: 0.3rem;
`;

const TextCount = styled.p`
  color: white;
  font-size: 1.4rem;
  white-space: nowrap;
  margin-bottom: 2rem;
`;

const Text = styled.p`
  color: white;
  font-size: 1.2rem;
  white-space: nowrap;
  margin-bottom: 2rem;
`;

const AlertComponent = (props) => {
  const [count, setCount] = useState(5);
  const saveCallback = useRef();

  const handleClose = () => {
    props.Close();
  };

  function callbackCount() {
    setCount(count - 1);
    if (count === 0) {
      handleClose();
    }
  }

  useEffect(() => {
    saveCallback.current = callbackCount;
  });

  useEffect(() => {
    function tick() {
      saveCallback.current();
    }

    const timing = setInterval(tick, 1000);
    return () => clearInterval(timing);
  }, []);
  return (
    <Card onClick={handleClose}>
      <span className="bounceIn">
        <DoneGreen />
      </span>
      <Thankyou>/</Thankyou>
      <TextCount> {count} </TextCount>
      <Text>(   )</Text>
    </Card>
  );
};

export default AlertComponent;
